.background {
  background: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4)) ), url('../image/Intro.jpg');
  background: -webkit-linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('../image/Intro.jpg');
  background: -o-linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('../image/Intro.jpg');
  background: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('../image/Intro.jpg');
  min-height: 100vh;
  margin: 0px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; 
}

.maintext {
  color: white;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.font1 {
  font-size: 40px;
  font-family: 'Lato';
  font-weight: bold;
}

.font2 {
  font-size: 40px;
  font-family: 'Lato';
  color: #f1c40f;
  font-weight: bold;
}

.font3 {
  font-size: 15px;
  font-family: 'Lato';
  font-weight: bold;
}

.font4 {
  font-size: 10px;
  font-family: 'Lato';
}

.fonttyping {
  font-size: 40px;
  font-family: 'Lato';
}

.btnsignin {
  background-color: transparent;
  margin: 5px;
}

.navright {
  float: right;
}

.navleft {
  float: left;
}


